import * as React from 'react';
import Box from '@mui/material/Box';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/autoplay';
import { useStaticQuery, graphql } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import { borderRadius } from '../components/layout';

export const Slideshow = () => {
  const data = useStaticQuery(graphql`
    {
      allFile(
        filter: { relativePath: { regex: "/other//" } }
        sort: { relativePath: ASC }
      ) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(width: 320, layout: FIXED)
          }
        }
      }
    }
  `);

  return (
    <Box
      sx={{
        '.swiper-wrapper': {
          transitionTimingFunction: 'linear'
        }
      }}
    >
      <Swiper
        modules={[Autoplay]}
        loop={true}
        speed={5000}
        allowTouchMove={false}
        autoplay={{
          delay: 0,
          disableOnInteraction: false
        }}
        breakpoints={{
          320: {
            slidesPerView: 1,
            spaceBetween: 10
          },
          640: {
            slidesPerView: 2,
            spaceBetween: 20
          },
          960: {
            slidesPerView: 3,
            spaceBetween: 30
          }
        }}
      >
        {data?.allFile?.nodes?.map((d: any, i: number) => {
          return (
            <SwiperSlide key={i}>
              <Box
                sx={{
                  borderRadius,
                  width: '100%',
                  height: {
                    xs: 280
                  },
                  overflow: 'hidden'
                }}
              >
                <GatsbyImage
                  image={getImage(d) as any}
                  alt=''
                  style={{
                    width: '100%',
                    height: '100%',
                    pointerEvents: 'none'
                  }}
                />
              </Box>
            </SwiperSlide>
          );
        })}
      </Swiper>
    </Box>
  );
};
